import React, { useState, useContext, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { Button, Card, CardBody, ListGroup, ListGroupItem, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import firebase from '../../../firebase/Firebase';
import { UserContext } from '../../../context/contexts';
import logo from '../../../assets/img/brand/logo.png';
import Spinner from '../../../components/spinner/Spinner';

function Login(props) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [passwordReset, setPasswordReset] = useState(false);
  const [loggingIn, setLoggingIn] = useState(false);
  const [errorMessage, setErrorMessage] = useState(null);

  const { uid, emailVerified } = useContext(UserContext);

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoggingIn(true);
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .then(userCredential => {
        setLoggingIn(false);

      })
      .catch((error) => {
        setLoggingIn(false);
        setErrorMessage(error.code + ". " + error.message);
      });
  }

  const forgotPassword = () => {
    firebase.auth().sendPasswordResetEmail(email).then(function () {
      alert("Password reset link sent to ", email);
    }).catch(function (error) {
      alert(error);
    });
  }


  return (

    <div className="app flex-row align-items-center">
      {uid && !emailVerified ? <Redirect to="/verify" /> : null}
      {uid && emailVerified ? <Redirect to="/market" /> : null}
      <Container>
        <Row className="justify-content-center">
          <Col md="8">
            <CardGroup>
              <Card className="p-4">
                <CardBody>
                  <Form onSubmit={handleSubmit}>
                    <Row>
                      <Col sm="12">
                        <img src={logo} />
                      </Col>
                    </Row>
                    {passwordReset ?
                      <React.Fragment>
                        <InputGroup className="mb-3">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="icon-user"></i>
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input type="text" name="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="Email" autoComplete="email" />
                        </InputGroup>
                        <Row>
                          <Col sm="12">
                            <Button onClick={forgotPassword} color="primary" className="px-4">Reset Password</Button>
                          </Col>
                        </Row>
                      </React.Fragment>
                      :
                      <React.Fragment>
                        <InputGroup className="mb-3 mt-4">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="icon-user"></i>
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input disabled={loggingIn} type="text" name="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="Email" autoComplete="email" />
                        </InputGroup>
                        <InputGroup className="mb-4">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="icon-lock"></i>
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input disabled={loggingIn} type="password" value={password} onChange={e => setPassword(e.target.value)} name="password" placeholder="Password" />
                        </InputGroup>

                        {loggingIn ?
                          <div className="text-center"><Spinner /></div>
                          :
                          <React.Fragment>
                            <Row>
                              <Col xs="6">
                                <Button type="submit" color="primary" className="px-4">Login</Button>
                              </Col>
                              <Col xs="6" className="text-right">
                                <Button onClick={() => setPasswordReset(true)} color="link" className="px-0">Forgot password?</Button>
                              </Col>
                            </Row>
                            <Row>
                              <Col>
                                <div className="mt-2 d-block d-lg-none">
                                  <Link to="/register">Register Account</Link>
                                </div>
                              </Col>
                            </Row>

                          </React.Fragment>
                        }
                        <strong className="mt-2 text-danger">{errorMessage}</strong>
                      </React.Fragment>
                    }

                  </Form>

                </CardBody>
              </Card>
              <Card className="text-white bg-primary py-5 d-none d-lg-block" style={{ width: '44%' }}>

                <CardBody >
                  <h3 className="text-center">Free Personal Account for Small Investors</h3>
                  <ListGroup >
                    <ListGroupItem className="bg-primary">Advanced Live Intraday and Daily Technical Charts with hundreds of indicators and tools.</ListGroupItem>
                    <ListGroupItem className="bg-primary">Free Advanced Portfolio Management with Live Price Changes and Graphs</ListGroupItem>
                    <ListGroupItem className="bg-primary">In Depth into Company Fundamentals and Reports</ListGroupItem>
                  </ListGroup>
                  <Link to="/register">
                    <Button size="lg" className="mt-3" color="info"><i className="fa fa-envelope"></i><span> Register with Email</span></Button>
                  </Link>
                  <br />
                  <small className="text-muted">*Contact us for Premium account if you are Large Investor or Fund Manager.</small>

                </CardBody>
              </Card>
            </CardGroup>
          </Col>
        </Row>
      </Container>
    </div>
  );
}


export default Login;
